	module.exports = {"data":{"markdownRemark":{"html":"<p>私は海外の「中国脅威理論」のレビュー記事を見た。中国経済が発展した後、この種の世論は徐々に上昇し、ある市場の理論となった。主に中国の将来は、世界で最も強力な包括的な経済力を持つ国であり、周辺諸国に脅威をもたらす可能性がある。中国は軍事力を発達させていると述べた。これは不思議な発言です。中国経済は発展しておらず、その周りには苦味があり、経済は発展し、恐ろしいです。中国周辺では、依然として中国よりも軍事力の強い国があるが、それは「脅威」ではない。日本の軍事費は世界で2番目であるが、「脅威」ではない。このようなパラドックスは、日本で一定の市場を持っています。</p>\n<p>この種の「中国脅威理論」は無視することができます。</p>","id":"/home/swain/workspace/political-life/src/pages/ja/0188.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/ja/0188.md","frontmatter":{"title":"11月17日 木曜日"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/ja/0188.md absPath of file >>> MarkdownRemark"}}

